@import url(https://fonts.googleapis.com/css?family=Lobster);

.notfound {
  margin: 50px; }

img {
  width: 100%;
  height: auto; }

h2 {
  margin-bottom: .5em; }

.grid {
  -webkit-columns: 200px 5;
          columns: 200px 5; }

.grid__item {
  position: relative;
  margin-bottom: 1rem;
  overflow: hidden;
  -webkit-animation: 1s linear 0s 1 normal none running anime;
          animation: 1s linear 0s 1 normal none running anime;
  background-color: rgba(37, 0, 10, 0.4); }
  .grid__item:hover {
    box-shadow: 3px 3px 2px 1px #0000009c; }

.image {
  line-height: 0;
  overflow: hidden; }

.image img {
  -webkit-filter: blur(0px);
          filter: blur(0px);
  transition: -webkit-filter 0.3s ease-in;
  transition: filter 0.3s ease-in;
  transition: filter 0.3s ease-in, -webkit-filter 0.3s ease-in;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  min-width: 250px; }

.title {
  font-family: 'Lobster', cursive;
  font-weight: normal;
  text-align: center;
  text-decoration: none;
  z-index: 1;
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  opacity: 0;
  transition: opacity .5s;
  background: rgba(90, 0, 10, 0.4);
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center; }
  .title img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    -webkit-filter: blur(0px);
            filter: blur(0px);
    transition: -webkit-filter 0.3s ease-in;
    transition: filter 0.3s ease-in;
    transition: filter 0.3s ease-in, -webkit-filter 0.3s ease-in;
